body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container {
  display: grid;
  padding: 1rem;
}

.notes-header {
  display: grid;
  place-items: center start;
  justify-content: start;
  grid-auto-flow: column;
  padding: 0.5rem;
  gap: 0.5rem;
}

.button {
  font-weight: bold;
  border: none;
  font-size: 1rem;
  padding: 0.5rem;
  border-radius: 0.2rem;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}

.color-blue {
  background: rgb(65, 180, 247);
  color: white;
}

.color-blue:hover {
  background: rgb(0, 1, 2);
  color: rgb(0, 255, 200);
}

.color-yellow {
  background: rgb(247, 229, 65);
  color: rgb(2, 5, 10);
}

.color-yellow:hover {
  background: rgb(0, 1, 2);
  color: rgb(251, 255, 0);
}

.color-not-important {
  background: rgb(187, 198, 212);
  color: white;
}

.color-not-important:hover {
  background: rgb(23, 224, 83);
  color: rgb(0, 0, 0);
}

.note-container {
  all: unset;
  padding: 1rem;
  display: grid;
  justify-content: center;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1rem));
  gap: 0.5rem;
  list-style-type: none;
}

.note-card {
  display: grid;
  place-items: center;
  width: 200px;
  padding: 1rem;
  gap: 0.5rem;

  border: 2px solid rgb(0, 191, 255);
  border-radius: 0.4rem;
}

.add-notes-form {
  display: grid;
  gap: 0.5rem;
  padding: 1rem;
  width: 250px;
}

.add-notes-input {
  padding: 0.5rem;
  font-size: 1rem;
  border: 2px solid rgb(108, 108, 134);
  border-radius: 0.4rem;
}

.error {
  justify-self: center;
  width: 250px;
  padding: 1rem;
  font-size: 1.2rem;
  border: 2px solid rgb(182, 53, 49);
  color: rgb(49, 9, 9);
}